.top-container {
  height: auto;
  margin: 9.313rem 5%;
  padding-bottom: 10rem;
  border-radius: 4px;
  box-shadow: 0 0 60px 0 black;
  background-color: white;
}

.bookcard-container {
  margin: 2.375rem 5% 0.938rem 5%;
}

.Book-Card {
  display: flex;
  justify-content: space-between;
  height: 9.625rem;
  padding: 2rem 1% 1.625rem 1%;
  margin-bottom: 1rem;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: white;
}

.title-section {
  width: 23rem;
}

.genres {
  opacity: 0.5;
  font-size: 0.875rem;
  color: #121212;
}

.title {
  margin: 0.188rem 0 0 0;
  font-size: 1.375rem;
  letter-spacing: -0.2px;
  color: #121212;
}

.author {
  margin: 0.25rem 0 0 0;
  font-size: 0.875rem;
  font-weight: 400;
  color: #4386bf;
}

.comRemEdit {
  display: flex;
}

.comRemEdit p {
  margin: 1.4rem 1rem 2rem 0;
  font-size: 0.875rem;
  font-weight: 300;
  color: #4386bf;
}

.up-bar {
  margin-top: 1.4rem;
  margin-right: 1rem;
  font-size: 17px;
  color: #c9baba;
}

.stat-section {
  display: flex;
  margin-top: 2.5rem;
}

.circle-container {
  width: 80px;
  height: 80px;
  background: #e1e1e1;
  border-radius: 50%;
}

.circle-container .circle .item,
.circle-container .circle .fill {
  width: 80px;
  height: 80px;
  position: absolute;
  border-radius: 50%;
}

.circle-container .circle .item {
  clip: rect(0, 100px, 100px, 44px);
}

.circle-container .circle .item.layer-1,
.circle-container .circle .fill {
  transform: rotate(126deg);
  animation: fill ease-in-out 3s;
}

.circle-container .circle .item .fill {
  clip: rect(0, 44px, 150px, 0);
  background-color: #2892d7;
}

@keyframes fill {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(126deg);
  }
}

.circle-container .circle .itm-lay-1,
.circle-container .circle .fill-1 {
  transform: rotate(8deg);
  animation: fill ease-in-out 3s;
}

@keyframes fill-1 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(8deg);
  }
}

.inside-content {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #eef3f0;
  margin-top: 10px;
  margin-left: 10px;
  position: absolute;
}

.percent-complete {
  display: flex;
  flex-direction: column;
  margin-left: 1.3rem;
}

.percent {
  font-size: 2rem;
  color: #121212;
}

.complete {
  opacity: 0.5;
  font-size: 0.875rem;
  color: #121212;
}

.update-section {
  display: flex;
  flex-direction: column;
  margin-right: 4rem;
  margin-top: 1rem;
}

.current-chapter {
  margin: 0.063rem 0 0.4rem 0;
  opacity: 0.5;
  font-size: 0.813rem;
  font-weight: 300;
  color: #121212;
}

.current-lesson {
  margin: 0.438rem 0 1.438rem 0;
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: -0.4px;
  color: #121212;
}

.update-progress {
  width: 11rem;
  height: 2.125rem;
  font-size: 0.813rem;
  font-weight: 400;
  border: none;
  letter-spacing: 0.5px;
  text-align: center;
  color: white;
  background-color: #0290ff;
}

.shadow {
  width: 75rem;
  height: 0.125rem;
  margin: 1.5rem 0.063rem 1.813rem 0;
  border: solid 2px var(--white-three);
}
